// item.id  o id do item que estamos trabalhando
// j  a semana

export const calculate = (items) => {

    const newItems = JSON.parse(JSON.stringify(items));

    newItems.forEach((item) => {
        if(item.pai !== -1) { // Se no for a prpria lapiseira atribui as entradas como sada do pai
            for(let j = 0;j<8;j++) {
                item.entradas[j] = newItems[item.pai].saida[j] * item.quantidadeReceita;
            }
        }
        if(item.calcular) {
            if(item.duplicado !== -1) { // Verifica se o item a ser calculado  duplicado, se for soma as entradas da duplicata q ja apareceu.
                for(let j = 0;j<8;j++) {
                    item.entradas[j] = item.entradas[j] + newItems[item.duplicado].entradas[j];
                }
            } // Clculos da semana 1 - diferente porque nesse caso lidamos com o estoque inicial
            if(item.estoqueInicial - item.entradas[0] >= item.estoqueSegurana) {
                item.estoqueProjetado[0] = item.estoqueInicial - item.entradas[0];
            }
            else {
                item.estoqueProjetado[0] = -1;
                // Pensar se lana excesso ou como tratar, porque se o lead time for maior que 1 semana no tem como fazer, 
                //adicionar recebimentos programados seria complexo, uma alternativa e deixar zerar e se faltar pea deixar como na prova q era faltante mesmo e segue
                // Talvez essa validao seja vlida at mesmo para semanas iniciais, se o Lead time maior que o prazo de semanas.
            }
            for(let j = 1;j<8 ;j++){ // Clculos para as demais semanas
                const resultEstoqueProjetado = item.estoqueProjetado[j-1] === -1 ? 0 - item.entradas[j] : item.estoqueProjetado[j-1] - item.entradas[j];
                if(resultEstoqueProjetado >= item.estoqueSegurana){
                    item.estoqueProjetado[j] = resultEstoqueProjetado;
                }
                else {
                    try{
                        let aux = 1 // Varivel para saber qual ser o multiplicador do lote mnimo
                        while((item.tamanhoLote * aux) +  resultEstoqueProjetado < item.estoqueSegurana){
                            aux++;
                        } // Tenta atribuir para a semana que deve ser pedido se ela existir
                        if (j - item.leadTime >= 0){
                            item.saida[j - item.leadTime] = item.tamanhoLote * aux;
                            item.recebimentosProgramados[j] = item.tamanhoLote * aux;
                            item.estoqueProjetado[j] = (item.tamanhoLote * aux) + resultEstoqueProjetado;
                        }
                        else{
                            item.estoqueProjetado[j] = -1;
                        }
                    } catch (e) {
                        item.estoqueProjetado[j] = -1;
                        // Pensar em como informar que o lead time  maior que o tempo que se tem para produzir, ou se vamos zerar o estoque como na prova
                    }
                }
            }
        }
    });

    let itemsPrint = [];
    for(let i = 0; i<23;i++){
        if (newItems[i].calcular){
            itemsPrint[i] = newItems[i];
        }
    }
    return itemsPrint;
}
